.cardsItem{width:340px;min-height:230px;display:flex;flex-direction:column;box-shadow:0px 0px 35px 0px rgba(0,0,0,.243);backdrop-filter:blur(12px);border-radius:25px;-webkit-backdrop-filter:blur(12px);background-color:hsla(0,0%,100%,.0117647059);user-select:none;cursor:pointer;transition:.2s}.cardsItem:hover{background-color:#fff}.cardsItem .cardHeader{display:flex;align-items:center;justify-content:space-between}.cardsItem .cardHeader .cardsTitle{margin:15px 25px 0 25px;font-size:32px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;line-height:40px}.cardsItem .cardHeader .redCross{display:none}.cardsItem .cardsBody{font-size:12px;margin:15px 25px;height:150px;position:relative;overflow:auto;-ms-overflow-style:none;scrollbar-width:none}.cardsItem .cardsBody::-webkit-scrollbar{display:none}.selected{box-shadow:0px 0px 35px 0px #ff2f54}.selected .cardHeader{position:relative}.selected .cardHeader .redCross{cursor:pointer;display:block;height:50px;width:50px;position:absolute;top:15%;bottom:0;left:86%;right:0}.selected .cardHeader .redCross:after{content:"‚ùå";font-size:25px;color:#fff;line-height:50px;text-align:center}