.loaderWrapper {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .loader {
        width: 70px;
        aspect-ratio: 1.154;
        --_g: no-repeat radial-gradient(farthest-side,#ffffff 90%,#ffffff00);
        background: 
          var(--_g) 50%  0,
          var(--_g) 0    100%,
          var(--_g) 100% 100%;
        background-size: 35% calc(35%*1.154);
        animation: l16 1s infinite;
      }
      @keyframes l16{ 
          50%,100% {background-position: 100% 100%,50% 0,0 100%} 
      }
}
